<template>
  <div class="page-payment">
    <div class="payment-content">
      <!-- 卡片盒子 -->
      <div class="card-box">
        <div class="text__info">课程信息</div>
        <!-- 第一行 -->
        <div class="flex__space verCenter mt__40">
          <div class="title">12天小白理财训练营</div>
          <div class="verCenter">
            <div class="money__small mr__10">¥12</div>
            <!-- <div class="text__through">市场价¥199元</div> -->
          </div>
        </div>
        <!-- 第二行 -->
        <div class="flex__space verCenter mt__20">
          <div class="text__info">支持开课前3天无理由退款</div>
          <div class="text__number">X1</div>
        </div>
        <!-- 内容盒子 -->
        <div class="mt__30">
          <div class="card-content">
            <div class="card-content-imgBox">
              <img src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/BaiduPcPayOrder/classImg.png" />
            </div>
            <div class="card-content-text ml__30">
              <div
                class="verCenter"
                v-for="(item, index) in cardTextList"
                :key="index">
                <img
                  class="circle-check"
                  :src="require('@/assets/Payment/success.png')" />
                <div class="text__desc">{{ item }}</div>
              </div>
            </div>
          </div>
          <!-- 线 -->
          <div class="line"></div>
        </div>
      </div>
    </div>
    <!-- 底部按钮 -->
    <div class="btn-box">
      <div class="ml__50">
        <span class="money-icon">¥</span>
        <span class="money__big">12</span>
      </div>
      <div
        class="btn__pay mr__20 "
        @click="goTeacherQrCode">确认支付</div>
    </div>
  </div>
</template>

<script>
import Store from '@/store'
import wxAuth from '@/helper/Auth/wxAuth'
import { clickLog } from '@/helper/clickLog'
import { postBack } from '@/helper/payConfig'
import { getSearch } from '@/utils'
// 是否需要ab测数据
function getABDetail () {
  if (getSearch('uniqFlag')) {
    const detail = {
      domain: getSearch('domain') || undefined,
      ab_key: getSearch('abKey') || undefined,
      ab_group: getSearch('group') || undefined,
      ab_uniq_user: getSearch('uniqFlag') || undefined
    }
    return JSON.stringify(detail)
  }
  return undefined
}
// 支付得AB测数据
function payAbDetail () {
  if (getSearch('uniqFlag')) {
    const detail = {
      domain: getSearch('domain') || undefined,
      abKey: getSearch('abKey') || undefined,
      group: getSearch('group') || undefined,
      uniqFlag: getSearch('uniqFlag') || undefined
    }
    return [detail]
  }
  return undefined
}
export default {
  data () {
    return {
      disabledPayLoading: false, // 禁用支付
      cardTextList: [
        // 卡片说明文字
        '专为0基础设计',
        '12天线上授课',
        '1对1全程答疑'
      ]
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({ getTokenQuery: postBack() })
    const { id, wx_body: { openid }, unionid } = Store.state.user.userInfo || {}
    const updateLog = {
      mid: id,
      openid,
      unionid
    }
    const alternate = getABDetail()
    if (alternate) {
      updateLog.alternate = alternate
    }
    Store.$log.update(updateLog)
    Store.$apis.payment.checkOrderInWx().then(() => {
      next()
    }).catch(err => {
      if (err.code === 30009) {
        next({ name: 'BaiduPcTeacherQrCode' })
      }
      next()
    })
  },
  created () {
    wx.ready(() => {
      wx.hideAllNonBaseMenuItem()
    })
    const { goTeacherQrCode } = this
    if (typeof window.WeixinJSBridge === 'undefined') {
      if (document.addEventListener) {
        document.addEventListener('WeixinJSBridgeReady', goTeacherQrCode, false)
      } else if (document.attachEvent) {
        document.attachEvent('WeixinJSBridgeReady', goTeacherQrCode)
        document.attachEvent('onWeixinJSBridgeReady', goTeacherQrCode)
      }
    }
  },
  methods: {
    /**
     * @Date: 2020-12-24 19:06:19
     * @author: zjs
     * @description: 去添加老师二维码页面
     */
    async goTeacherQrCode () {
      try {
        clickLog({ module_ori: '支付模块', module_part: '106_toufang_lcx_0130' })
        if (this.disabledPayLoading) return
        const { origin, pathname } = location
        this.disabledPayLoading = true
        const result = await this.$apis.payment.payInWx({
          pay_type: 2,
          pay_mode: 3,
          ...postBack(),
          ext_param: payAbDetail(),
          return_url: decodeURIComponent(`${origin}${pathname}#/BaiduPcTeacherQrCode`)
        })
        window.WeixinJSBridge.invoke('getBrandWCPayRequest', result,
          (res) => {
            this.disabledPayLoading = false
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              const { query } = this.$route
              this.$router.push({ name: 'BaiduPcTeacherQrCode' }, query)
            }
          })
      } catch (error) {
        this.disabledPayLoading = false
      }
    }
  }
}
</script>

